.section {
  padding: 20px 0;
}

.title {
  margin-bottom: 20px;
  font-size: 25px;
  text-align: center;
}

.hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
